export default {
	data(){
		return {
			userId:"",
			page:1,
			dataList:{},
			noData:"more"
		}
	},
	onShow(){
		this.$request.GET({
			url:this.$api.apiUrl.GET_USER_INFO,
		}).then(res=>{
			this.userId = res.data.id;
			this.init();
			this.reqSellDeail();
		})
	},
	onReachBottom(){
		if(this.noData === 'more'){
			this.reqSellDeail();
		}else{
			return false;
		}
	},
	methods:{
		init(){
			this.page = 1;
			this.dataList = {};
		},
		reqSellDeail(){
			this.noData = 'loading';
			this.$request.POST({
				url:this.$api.apiUrl.POST_V4_USER_SELL_DETAIL,
				data:{
					user_id:this.userId,
					page:this.page
				}
			}).then(res=>{
				if(res.code === 200){
					if(res.data){
						this.noData = 'more';
						// 获取返回对象的长度
						const arr = Object.keys(res.data);
						if(arr.length < 8){
							this.noData = 'noMore';
						}else{
							// 如果还有数据就让page+1
							this.page++;
							this.noData = 'more';
						}
						// 合并对象
						this.dataList = Object.assign(this.dataList,res.data);
					}else{
						this.noData = 'noMore'
					}
				}else{
					this.$api.msg("数据异常");
					this.noData = 'noMore'
				}
			})
		}
	}
}
